import {
    Component,
    ViewChild,
} from '@angular/core';
import { NavController } from 'ionic-angular';

@Component({
    selector: 'page-home',
    templateUrl: 'home.html'
})
export class HomePage {

    public music_on: boolean = true

    @ViewChild('music') public music: any

    public nodes: any = {
start: {
html:
`
我只是看看中文能不能打出来~
<br>顺便试试换行
`,
options: [
{
    html: '你有毒吧',
    color: 'primary',
    next: 'some1'
},
{
    html: 'do that please',
    color: 'danger',
    next: 'some2'
},
]
},
some1: {
html: 'asdf',
options: [
{
    html: 'do this ',
    color: 'primary',
    next: 'start'
},
{
    html: 'do that lorem',
    color: 'primary',
    next: 'some2'
},
]
},
some2: {
html: 'asrewer',
options: [
{
    html: 'do this please',
    color: 'primary',
    next: 'start'
},
]
},
}

    public current_node: object = this.nodes.start

    constructor(public navCtrl: NavController) {
        
    }

    public toggle_music() {
        if (this.music_on) {
            this.music.nativeElement.pause()
            this.music_on = false
        }
        else {
            this.music.nativeElement.play()
            this.music_on = true
        }
    }

    public change_node(node_name) {
        this.current_node = this.nodes[node_name]
    }

}




